'use client'
import { useState } from 'react'
import Link from 'next/link'

export default function Navbar() {
  const [mobileOpen, setMobileOpen] = useState(false)

  const navLinks = [
    // { name: 'Home', href: '/' },
    { name: 'About', href: '/about' },
    { name: 'Experience', href: '/experience' },
    { name: 'Projects', href: '/projects' },
    { name: 'Skills', href: '/skills' },
    { name: 'Education', href: '/education' },
    { name: 'Services', href: '/services' },
    { name: 'Contact', href: '/contact' },
    { name: 'Resume', href: '/resume' }
  ]

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 bg-slate-950/95 backdrop-blur-md border-b border-slate-800/50">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex h-20 items-center justify-between">
          {/* Logo */}
          <Link href="/" className="text-3xl font-black bg-gradient-to-r from-blue-400 to-emerald-400 bg-clip-text text-transparent hover:scale-105 transition-transform">
            SK
          </Link>
          
          {/* Desktop Menu - 2 rows for 9 links */}
          <div className="hidden md:block">
            <ul className="flex flex-col sm:flex-row items-center space-y-2 sm:space-y-0 sm:space-x-2 lg:space-x-4">
              {navLinks.map((link, index) => (
                <li key={link.name}>
                  <Link 
                    href={link.href} 
                    className="px-3 py-2 text-base lg:text-lg font-semibold text-slate-300 hover:text-white hover:bg-slate-800/50 rounded-xl transition-all duration-300 group"
                  >
                    <span className="block">{link.name}</span>
                  </Link>
                </li>
              ))}
            </ul>
          </div>

          {/* Mobile Menu Button */}
          <button 
            className="md:hidden p-2 rounded-xl text-slate-300 hover:text-white hover:bg-slate-800/50 transition-all"
            onClick={() => setMobileOpen(!mobileOpen)}
            aria-label="Toggle mobile menu"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </button>
        </div>

        {/* Mobile Menu - Full screen overlay */}
        {mobileOpen && (
          <div className="md:hidden absolute top-20 left-0 right-0 bg-slate-950/95 backdrop-blur-md border-t border-slate-800 py-8 shadow-2xl">
            <div className="max-w-2xl mx-auto px-4">
              <ul className="grid grid-cols-2 gap-4 text-center">
                {navLinks.map((link) => (
                  <li key={link.name}>
                    <Link 
                      href={link.href} 
                      className="block px-6 py-4 text-xl font-bold text-slate-300 hover:text-white hover:bg-slate-800/80 rounded-2xl transition-all duration-300 transform hover:scale-105"
                      onClick={() => setMobileOpen(false)}
                    >
                      {link.name}
                    </Link>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        )}
      </div>
    </nav>
  )
}
